.Home {
  background-color: blueviolet;

  width: 100vw; /* Asegura que el contenedor cubra el ancho de la pantalla */
  height: 100vh; /* Asegura que el contenedor cubra la altura de la pantalla */
  margin: 0; /* Elimina el margen predeterminado del body */
  padding: 0; /* Elimina el relleno predeterminado del body */
}
/* Paginado container */
.Paginado {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}

/* Estilo para los botones de página */
.Paginado button {
  padding: 5px 10px;
  margin: 0 5px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.Paginado button:hover {
  background-color: #0056b3;
}

/* Estilo para el botón activo de la página actual */
.Paginado button.active {
  background-color: #0056b3;
}

/* Estilo para los botones "Prev" y "Next" */
.Paginado button.prevnext {
  background-color: #ccc;
}

/* Estilo para el texto "Prev" y "Next" */
.Paginado .prevnext-text {
  font-weight: bold;
  margin-right: 5px;
}
